*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-family: Raleway, sans-serif;
  font-size: 62.5%;
}

body {
  background-color: #f7f3e3;
  font-size: 1.8rem;
}

@media only screen and (width <= 75em) {
  html {
    font-size: 50%;
  }
}

.margin--low {
