  align-items: center;
  width: 100%;
  height: 20rem;
  display: flex;
  position: relative;
}

.header:before, .footer:before {
  content: "";
  clip-path: polygon(45% 0, 100% 0, 100% 100%, 55% 100%);
  z-index: 2;
  background-color: #000c;
  position: absolute;
  inset: 0;
}

.header__description, .footer__description {
  color: #fff;
  padding: 2rem, 2rem, 2rem, 2rem;
  z-index: 3;
  font-size: 4rem;
  font-weight: 300;
  position: absolute;
  left: 10%;
}

.section {
  justify-content: center;
  align-items: center;
