}

.section__gallery .pizza-item {
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 46rem;
  padding: .5rem;
  transition: transform .3s;
  display: flex;
  position: absolute;
  box-shadow: 0 4px 8px #0003;
}

.section__gallery .pizza-item-1 {
  top: 5%;
  left: 0;
}

.section__gallery .pizza-item-2 {
  bottom: 9%;
  right: 0%;
}

.section__gallery .pizza-item-3 {
  top: 50%;
  left: 5%;
}

@media (width <= 700px) {
  .section__gallery .pizza-item-1 {
    width: 100%;
    position: static;
  }

  .section__gallery .pizza-item-2, .section__gallery .pizza-item-3, .section__gallery .pizza-item-2 img, .section__gallery .pizza-item-3 img {
    width: 0;
    height: 0;
    display: none;
  }
}

.section__gallery .pizza-item:hover {
  transform: scale(1.05);
}

.section__gallery .pizza-item img {
  object-fit: cover;
  border-radius: 8px;
  width: 100%;
  height: auto;
}

@media (width <= 700px) {
  .section__gallery {
    height: max-content;
    padding-top: 8rem;
